<template>
  <v-container>
    <div
      style="padding-right: 25px; border-right-style: solid; border-color: white; border-width: 1px; display:inline-block; margin-top: 60px; margin-bottom: 40px"
    >
      <h4 class="white--text" style="background-color: transparent; font-family: 'Jura', sans-serif;">
        CONTACT
      </h4>
    </div>
    <v-layout justify-center class="my-5 py-5" flat>
      <v-flex md6 xs12 flat>
        <v-card flat dark>
          <v-form
            accept-charset="UTF-8"
            class="ma-2 pa-2"
            ref="form"
            action="https://getform.io/f/d834cc39-96a8-472e-981a-0dfe60614b6d"
            method="POST"
            enctype="multipart/form-data"
            id="ajaxForm"
            v-model="valid"
            lazy-validation
            v-on:submit.prevent="submit()"
          >
            <!--NAME-->
            <v-text-field
            style="font-family: 'Jura', sans-serif;"
              label="Name"
              v-model="name"
              :rules="nameRules"
              prepend-icon="mdi-account"
              :counter="20"
              :clearable="clearable"
              required
            ></v-text-field>
            <!--EMAIL-->
            <v-text-field
            style="font-family: 'Jura', sans-serif;"
              label="Email"
              v-model="email"
              :rules="emailRules"
              prepend-icon="mdi-email"
              :clearable="clearable"
              required
            ></v-text-field>
            <!--MESSAGE-->
            <v-textarea
              label="Message"
              v-model="message"
              prepend-icon="mdi-message"
              :clearable="clearable"
              :rules="msgRules"
              required
              style="font-family: 'Jura', sans-serif;"
            ></v-textarea>
            <v-btn
              :loading="loading"
              :disabled="disabled"
              color="success"
              class="mr-4"
              @click="submit"
              :outlined="outlined"
              block
              style="font-family: 'Jura', sans-serif;"
            >
              Submit
            </v-btn>
            <v-snackbar v-model="snackbar" color="succes" :timeout="0">
              <span>Your mesagge has been succesfully submited!</span>
              <v-btn v-on:click="snackbar = false">Close</v-btn>
            </v-snackbar>
          </v-form> </v-card
        ><br /><br />

        <v-card flat dark>
          <v-flex row justify-center justify-space-around>
            <div>
              <v-img
                cover
                src="../assets/img/WhatsApp_Icon.png"
                alt="Whatsapp-Icon"
              ></v-img>
            </div>
            <div>
              <p style="background-color: transparent; font-family: 'Jura', sans-serif;">+32 470 284743</p>
            </div>
            <div>
              <v-img
                src="../assets/img/telegram-small.png"
                alt="Telegram-Icon"
              ></v-img>
            </div>
          </v-flex>
          <v-flex row justify-center>
            <div class="ma-auto">
             <a target="_blank" href="https://github.com/vortexxi"><v-img src="../assets/img/github.png" alt="GitHub"></v-img></a>
           </div>
           </v-flex>   
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<style type="text/css">
.v-card {
  display: inline;
}
</style>

<script type="text/javascript">
import axios from "axios";

export default {
  data() {
    return {
      valid: true,
      clearable: true,
      snackbar: false,
      outlined: true,
      message: "",
      loading: false,
      name: "",
      email: "",
      disabled: false,
      icons: [
      require('../assets/img/icons8-autograph-50.png'),require('../assets/img/icons8-autograph-50.png'),require('../assets/img/icons8-autograph-50.png')
      ],
      nameRules: [
        value => !!value || "Name is required",
        value =>
          (value && value.length <= 20) ||
          "Name must be less than 20 characters"
      ],
      emailRules: [
        value => !!value || "E-mail is required",
        value => /.+@.+\..+/.test(value) || "E-mail must be valid"
      ],
      msgRules: [
        value => !!value || "Name is required",
        value =>
          (value && value.length >= 20) || "Maybe more than 20 characters?"
      ]
    }; //return
  }, //data
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        //   nativeForm.submit();
        //   this.disabled = true;
        //   this.snackbar = true;
        //   this.$refs.form.reset();
        let data = {
          name: this.name,
          email: this.email,
          message: this.message
        }; //data
        axios
          .post(
            "https://getform.io/f/d834cc39-96a8-472e-981a-0dfe60614b6d",
            data,
            {
              headers: {
                Accept: "application/json"
              }
            }
          )
          .then(
            response => {
              this.isSuccess = response.data.success ? true : false;
              this.loading = false;
              this.disabled = true;
              this.snackbar = true;
              this.$refs.form.reset();
            }
            // response => {
            // Error
            //}
          ); //then
      } //if
    } //submit
  }, //methods
  props: {
    msg: String
  } //props
}; //export
</script>
